/* ==========
CASE STUDY IEP Wires
========== */

.card-iep-wires {
    @include flex-column;
    justify-content: center;

    width: calc(100vw - 16rem);
    height: auto;
    margin: 4rem 8rem 0 8rem;
    padding: 4rem 0 0 8rem;


    .title {
        @include folio-card-title;
        color: #000dd0;

        &:before {
            background-color: #bbb;
        }
    }

    .content-row {
        @include flex-row;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
    }

    .content {
        width: 50%;
        max-width: 48rem;
    }

    .wire-group {
        @include flex-row;
        justify-content: space-between;
        width: 50%;
        margin-left: 4rem;
    }

    .wire {
        @include flex-column;
        justify-content: center;
        width: 48%;
        border-radius: .4rem;
        background-color: #999;
        overflow: hidden;
        @include shadow01;
    }
}



// RESPONSIVE
@media screen and (max-width: 1400px) {

    .card-iep-wires {
        width: calc(100vw - 8rem);
        margin: 0 4rem;
        padding: 4rem 0 4rem 4rem;
    }
}

@media screen and (max-width: 1100px) {

    .card-iep-wires {
        p:last-child {
            margin-bottom: 0;
        }
    }
}


@media screen and (max-width: 960px) {

    .card-iep-wires {
        .content {
            width: 70%;
            max-width: 48rem;
        }

        .wire-group {
            @include flex-column;
            width: 30%;
        }

        .wire {
            @include flex-column;
            justify-content: center;
            width: 100%;
            height: auto;

            &:first-child {
                margin-bottom: 1.2rem;
            }
        }

    }
}

@media screen and (max-width: 680px) {

    .card-iep-wires {
        width: calc(100vw - 3.2rem);
        margin: 4rem 1.6rem 0;
        padding-right: 4rem;

        .content-row {
            @include flex-column;
        }

        .content {
            width: 100%;
            max-width: unset;
        }

        .wire-group {
            @include flex-row;
            justify-content: space-between;
            width: 100%;
            margin: 2.4rem 0 0 0;
        }

        .wire {
            @include flex-column;
            justify-content: center;
            width: 48%;
            height: auto;

            &:first-child {
                margin-bottom: 0;
            }
        }
    }
}

@media screen and (max-width: 480px) {

    .card-iep-wires {
        padding-right: 2.4rem;
        padding-left: 2.4rem;

        .title {
            font-size: 3.2rem;
        }

        .content-row {
            margin-top: 0;
        }

        .wire-group {
            @include flex-column;
            justify-content: flex-start;
        }

        .wire {
            width: 100%;

            &:first-child {
                margin-bottom: 1.2rem;
            }
        }
    }
}