/* ==========
CASE STUDY IEP Structure
========== */

.card-iep-structure {
    @include flex-column;
    justify-content: center;

    width: calc(100vw - 16rem);
    height: calc(100vh - 8rem);
    margin: 4rem 8rem;
    padding: 4rem 0 4rem 8rem;
    background-color: #f2efef;
    position: relative;

    .title {
        @include folio-card-title;
        font-size: 4.8rem;

        &:before {
            background-color: #00b7ff;
        }
    }

    .content-row {
        @include flex-row;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
        margin-top: 4rem;
    }

    .content {
        width: 45%;
        padding-right: 4rem;
    }

    .link-inline {
        &:before {
            background-color: rgba(0, 183, 255, .3);
        }
    }

    .diagram {
        width: 35%;
        padding-right: 4rem;

        svg {
            width: 100%;
        }
    }

    .thumb {
        width: 20%;
    }

}



// RESPONSIVE
@media screen and (max-height: 800px) {
    .card-iep-structure {
        height: auto;
    }
}

@media screen and (max-width: 1400px) {

    .card-iep-structure {
        width: calc(100vw - 8rem);
        margin: 0 4rem 4rem 4rem;
        padding: 4rem 0 4rem 4rem;
    }
}

@media screen and (max-width: 1100px) {
    .card-iep-structure {
        height: auto;

        .diagram {
            width: 40%;
        }

        .content {
            width: 60%;
        }

        .thumb {
            display: none;
        }

    }
}

@media screen and (max-width: 960px) {

    .card-iep-structure {
        padding: 4rem;

        .content-row {
            @include flex-column;
            align-items: center;
        }

        .diagram {
            order: 2;
            width: 100%;
            max-width: 48rem;
            padding-right: 0;
        }

        .content {
            order: 1;
            width: 100%;
            padding-right: 0;
        }

    }
}

@media screen and (max-width: 680px) {

    .card-iep-structure {
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem 4rem 1.6rem;
    }
}

@media screen and (max-width: 480px) {

    .card-iep-structure {
        padding-right: 2.4rem;
        padding-left: 2.4rem;

        .title {
            font-size: 3.2rem;
        }

        .content-row {
            margin-top: 0;
        }
    }
}