/* ==========
CASE STUDY IEP Prototyping card
========== */

$card-color: #715045;

.card-iep-prototyping {
    @include flex-column;
    justify-content: center;

    width: calc(100vw - 16rem);
    height: auto;
    margin: 4rem 8rem;
    padding: 4rem 8rem;
    background: $card-color;
    position: relative;

    .content-row {
        @include flex-row;
        justify-content: space-between;
        width: 66%;
    }

    .content {
        width: 100%;
        max-width: 48rem;
        margin-right: 4rem;
    }

    .title {
        @include folio-card-title;

        &:before {
            background-color: #00b7ff;
        }
    }

    p {
        color: #fff;
    }

    .link-inline {
        color: #fff;

        &:before {
            background-color: rgba(123, 215, 252, .3);
        }
    }

    .pullquote {
        width: 100%;
        max-width: 40rem;
        font-weight: 600;
        font-size: 2.8rem;
        line-height: 1.2;
    }

    .image {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 30%;
        overflow: hidden;

        .svg-mask {
            fill: $card-color;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            height: 100vh;
            width: auto;
        }

        img {
            width: auto;
            height: 100vh;
        }
    }

}

// RESPONSIVE
@media screen and (max-width: 1400px) {

    .card-iep-prototyping {
        width: calc(100vw - 8rem);
        margin: 0 4rem;
        padding: 4rem;
    }
}

@media screen and (max-width: 1100px) {

    .card-iep-prototyping {

        .content-row {
            @include flex-column;
            justify-content: flex-start;
        }

        .pullquote {
            align-self: flex-end;
        }
    }

}


@media screen and (max-width: 960px) {

    .card-iep-prototyping {
        padding-bottom: 22rem;

        .title {
            width: 100%;
            max-width: 60rem;
            align-self: center;
        }

        .content-row {
            @include flex-column;
            justify-content: flex-start;
            align-items: center;
            width: 100%;
        }

        .content {
            max-width: 60rem;
            margin: 0;
        }

        .pullquote {
            align-self: center;
        }

        .image {
            position: absolute;
            top: auto;
            right: 0;
            bottom: 0;
            height: 20rem;
            width: 100%;
            overflow: hidden;

            .svg-mask {
                display: none;
            }

            img {
                width: 100%;
                height: auto;
            }
        }
    }
}

@media screen and (max-width: 680px) {

    .card-iep-prototyping {
        width: calc(100vw - 3.2rem);
        margin: 0 1.6rem;
        padding-right: 4rem;

    }
}

@media screen and (max-width: 480px) {

    .card-iep-prototyping {
        padding-right: 2.4rem;
        padding-left: 2.4rem;

        .title {
            font-size: 3.2rem;
        }

        .content-row {
            margin-top: 0;
        }

    }
}