/* ==========
CASE STUDY IEP Designs
========== */

.card-iep-designs {
    @include flex-column;
    justify-content: center;

    width: 100vw;
    height: auto;
    margin: 0 0 4rem 0;
    padding: 4rem 0;

    .title {
        @include folio-card-title;

        &:before {
            background-color: #bee600;
        }
    }

    .content-row {
        @include flex-row;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        width: 100%;
    }

    .content {
        width: 100%;
        max-width: 80rem;
        margin: 0;
        padding: 0 4rem 0 16rem;
    }

    .link-inline {
        &:before {
            background-color: rgba(190, 230, 0, .3);
        }
    }

    .mask {
        width: 100%;
        overflow-x: scroll;
    }

    .thumbs-list {
        @include flex-row;
        width: 240rem;
        padding: 0 16rem;
        list-style: none;

        li {
            margin-right: 4rem;
        }
    }

    .wire {
        width: 40rem;
        height: 22rem;
        border-radius: .4rem;
        overflow: hidden;
        @include shadow01;
    }

    .caption {
        margin-top: .8rem;
        color: #666;
    }
}


// RESPONSIVE
@media screen and (max-width: 1400px) {

    .card-iep-designs {
        .content {
            max-width: 72rem;
            padding-left: 8rem;
        }

        .thumbs-list {
            padding-left: 8rem;
        }
    }
}

@media screen and (max-width: 680px) {

    .card-iep-designs {
        .content {
            margin: 0;
            padding: 0 4rem 0 6rem;
        }

        .thumbs-list {
            padding: 0 16rem 0 6rem;
        }
    }
}

@media screen and (max-width: 480px) {

    .card-iep-designs {
        .content {
            padding: 0 4rem;
        }

        .thumbs-list {
            padding: 0 16rem 0 2rem;
        }
    }
}