/* ==========
TYPOGRAPHY
========== */

html {
  font-size: 62.5%;
  font-weight: 400;
  font-style: normal;
  padding: 0;
  margin: 0;
}

@mixin app-font {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

body {
  @include app-font;
  color: $primary-1;
  font-size: 1.4rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4 {
  @include app-font;
  font-weight: 600;
  font-style: normal;
}

// h2 level
@mixin folio-card-title {
  font-size: 4rem;
  line-height: 1.1;
  margin-top: 3.2rem;
  margin-bottom: 3.2rem;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: -3.2rem;
    left: 0;
    height: 0.8rem;
    width: 7.2rem;
  }
}

// h3 level
.folio-card-sub-title {}

// h4 level
.folio-card-component-title {}


p {
  font-size: 1.8rem;
  line-height: 1.4;
  margin-bottom: 2.4rem;
}

a {
  color: $primary-1;
  position: relative;

  &:hover {
    text-decoration: none;
    color: inherit;
  }
}

.link-inline {
  display: inline-block;
  position: relative;

  &:before {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(252, 252, 2, .5);
    transform-origin: bottom;
    transform: scaleY(.1);
    @include transition02;
    content: '';
  }

  &:hover:before {
    transform: scaleY(1);
  }
}